/* /components/OtherExperiences.css */
.other-experiences-section {
  margin-top: 50px;
  padding: 20px;
  font-family: Arial, sans-serif;
  background-color: #fff;  /* White background */
  border: 1px solid #ddd;  /* Light border around the box */
  border-radius: 8px;  /* Rounded corners */
}

.other-experiences-section h3 {
  color: #0077b5;
}

.other-experiences-section h4 {
  margin-top: 20px;
  color: #333;
}

.other-experiences-section p {
  color: #555;
}
